body {
  margin: 0;
  font-family: "Courier New", Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(72, 249, 172), rgb(137, 82, 255));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}

.Tilt {
    background: linear-gradient(to right, rgb(96, 0, 177), rgb(29, 251, 220));
}
.form {
    width: 700px;
    background-color: #e5e5f7;
    opacity: 1;
    background-image: radial-gradient(circle at center center, #fffdba, #f0f0f0), repeating-radial-gradient(circle at center center, #fff700, #fff700, 10px, transparent 20px, transparent 10px);
    background-blend-mode: multiply;
}

.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 2px #ff0000 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
